import EcProyectImg from '~/components/EcProyectImg.vue'
export default {

  name: 'ProyectInfo',

  components: { EcProyectImg },

  data () {
    return {
      gallery: false,
      imagevalue: 0,
      isImagenActive: true,
      isPlaneActive: false,
      al: {
        hasGrayscale: true,
        itemsToShow: 2,
        breakpoints: {
          768: {
            hasGrayscale: false,
            itemsToShow: 4
          },
          960: {
            hasGrayscale: true,
            itemsToShow: 6
          }
        }
      }
    }
  },

  computed: {
    proyect () {
      return this.$store.state.proyects[`${this.$route.params.id}`]
    },
    images () {
      return this.$store.state.proyects[`${this.$route.params.id}`].images
    },
    planes () {
      return this.$store.state.proyects[`${this.$route.params.id}`].planes
    }
  },

  methods: {
    switchGallery (value) {
      this.gallery = value
      if (value) {
        document.documentElement.classList.add('is-clipped')
      } else {
        document.documentElement.classList.remove('is-clipped')
      }
    }
  }
}
